img {
    max-width: 100%;
    height: auto;
  }
  
.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }

.page__body {
    height: 100%;
    font-size: 18px;
    font-family: "Open Sans", "Arial", sans-serif;
    line-height: 27px;
    margin: 0 auto;
    width: 320px;
    // background-color: $color-white;
  }

.main-page{
  background-color: #E5E5E5;
  padding: 30px 0;
}

